let code = {}

code.new = `
    /* 改变主题色变量 */
    $primary-color: green;

    /* 改变字体路径 */
    $icons-font-path: '~vsuite/src/styles/common/iconfont/fonts';//必须，也可以自定义字体路径，使用自己的字体图标
    
    /* 注意顺序，变量需要写在此行之前 */
    @import '~vsuite/src/styles/index.scss';
`
code.main = `
    import Vue from 'vue'
    import App from 'components/app.vue';
    import vsuite from 'vsuite'
    import './vsuite-variables.scss'

    Vue.use(vsuite)
`

export default code
